import React from 'react'
import { TextField } from '../../atoms/TextField'
import { useTranslation } from 'react-i18next'
import { useFormContext } from 'react-hook-form'

export interface FixedIpConfig {
  fixedIpAddress: string
  fixedSubnetMask: string
  fixedDefaultGateway: string
  fixedDnsServer: string
}

const ipAddressRegex = /^(([1-9]?[0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])\.){3}([1-9]?[0-9]|1[0-9]{2}|2[0-4][0-9]|25[0-5])$/
const subnetAddressRegex = /^(((0|192|224|240|248|252|254)(\.0){3})|(255\.(0|192|224|240|248|252|254)(\.0){2})|((255\.){2}(0|192|224|240|248|252|254)\.0)|((255\.){3}(0|192|224|240|248|252|254|255)))$/

export const FixedIpConfigure: React.FC = () => {
  const { t } = useTranslation()
  const { watch, register, errors } = useFormContext<FixedIpConfig>()
  watch('fixedIpEnabled', false)
  return (
    <div className="flex flex-col sm:grid sm:grid-cols-2 space-y-2 sm:space-y-0 sm:gap-4 m-2 ml-12">
      <TextField
        name="fixedIpAddress"
        title={t("LiveShell's IP address")}
        placeholder={t('e.g. 192.168.1.10')}
        autoComplete={'off'}
        ref={register({
          required: true,
          pattern: ipAddressRegex,
        })}
        error={
          errors.fixedIpAddress?.type == 'required'
            ? t('This field is required.')
            : errors.fixedIpAddress?.type == 'pattern'
            ? t('Invalid format.')
            : undefined
        }
      />
      <TextField
        name="fixedSubnetMask"
        title={t('Subnet mask')}
        placeholder={t('e.g. 255.255.255.0')}
        autoComplete={'off'}
        ref={register({
          required: true,
          pattern: subnetAddressRegex,
        })}
        error={
          errors.fixedSubnetMask?.type == 'required'
            ? t('This field is required.')
            : errors.fixedSubnetMask?.type == 'pattern'
            ? t('Invalid format.')
            : undefined
        }
      />
      <TextField
        name="fixedDefaultGateway"
        title={t('Default gateway address')}
        placeholder={t('e.g. 192.168.1.1')}
        autoComplete={'off'}
        ref={register({
          required: true,
          pattern: ipAddressRegex,
        })}
        error={
          errors.fixedDefaultGateway?.type == 'required'
            ? t('This field is required.')
            : errors.fixedDefaultGateway?.type == 'pattern'
            ? t('Invalid format.')
            : undefined
        }
      />
      <TextField
        name="fixedDnsServer"
        title={t('DNS server address')}
        placeholder={t('e.g. 192.168.1.1')}
        autoComplete={'off'}
        ref={register({
          required: true,
          pattern: ipAddressRegex,
        })}
        error={
          errors.fixedDnsServer?.type == 'required'
            ? t('This field is required.')
            : errors.fixedDnsServer?.type == 'pattern'
            ? t('Invalid format.')
            : undefined
        }
      />
    </div>
  )
}
