import React from 'react'
import { IconType } from 'react-icons'
import styles from './index.module.css'

export type ButtonProps = {
  name: string
  label: string
  icon?: IconType
  size?: 'small' | 'medium' | 'large' | 'x-large'
  style?: 'primary' | 'secondary'
  submit?: boolean
  onClick?: (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void
}

export const Button = React.forwardRef<HTMLButtonElement, ButtonProps>(
  function CheckBoxContent(
    {
      name,
      label,
      icon,
      size = 'medium',
      style = 'primary',
      submit = false,
      onClick,
    },
    ref
  ) {
    const Icon = icon ?? (() => <div />)
    return (
      <button
        type={submit ? 'submit' : 'button'}
        name={name}
        className={`flex justify-center items-center cursor-pointer ${styles[style]} ${styles[size]}`}
        ref={ref}
        onClick={onClick}
      >
        <Icon className={`align-baseline ${styles.icon}`} />
        <span className="font-medium pl-0.5 pr-0.5">{label}</span>
      </button>
    )
  }
)
