export const generateWiFiIni = (
  ssid: string,
  password: string | undefined,
  stealthMode: boolean,
  EncryptionScheme: 'WPA' | 'WEP_Open' | 'WEP_Shared' | 'None' | undefined,
  fixedIpEnabled: boolean,
  fixedIpAddress?: string,
  fixedSubnetMask?: string,
  fixedDefaultGateway?: string,
  fixedDnsServer?: string
): string => {
  let config = `[WLAN1]\nESSID=${ssid}\n`
  if (
    password !== undefined &&
    password !== '' &&
    EncryptionScheme !== 'None'
  ) {
    if (stealthMode && EncryptionScheme === 'WPA') {
      config += `PSK=${password}\n`
    } else if (stealthMode && EncryptionScheme === 'WEP_Shared') {
      config += `WEP=${password}\nMODE=SHARED\n`
    } else {
      config += `WEP=${password}\n`
    }
  }
  if (
    fixedIpEnabled &&
    fixedIpAddress &&
    fixedSubnetMask &&
    fixedDefaultGateway &&
    fixedDnsServer
  ) {
    config += `IP=${fixedIpAddress};${fixedDefaultGateway};${fixedSubnetMask}\nDNS=${fixedDnsServer}\n`
  }

  return config
}

export const generateEtherIni = (
  fixedIpEnabled: boolean,
  fixedIpAddress?: string,
  fixedSubnetMask?: string,
  fixedDefaultGateway?: string,
  fixedDnsServer?: string
): string => {
  if (
    fixedIpEnabled &&
    fixedIpAddress &&
    fixedSubnetMask &&
    fixedDefaultGateway &&
    fixedDnsServer
  ) {
    return `[ETHER]\nIP=${fixedIpAddress};${fixedDefaultGateway};${fixedSubnetMask}\nDNS=${fixedDnsServer}\n`
  } else {
    return '[ETHER]\n'
  }
}

export const generateMobileIni = (
  mobileUserName: string,
  mobilePassword: string,
  mobileApn: string
): string => {
  return `[PPP]\nAPN=${mobileApn}\nNAME=${mobileUserName}\nPASS=${mobilePassword}\n`
}
