import { useState } from 'react'
import { LocalModeConfigForm } from './components/templates/LocalModeConfigure'
import {
  generateEtherIni,
  generateMobileIni,
  generateWiFiIni,
} from './networkSerializer'

export type LocalModeConfig = {
  device: 'LiveShellX' | 'LiveShellPro' | 'LiveShell2'
  configString: string
  form: LocalModeConfigForm | null
}

const networkFormToIni = (form: LocalModeConfigForm): string => {
  switch (form.networkType) {
    case 'wiFi':
      return generateWiFiIni(
        form.wiFiSsid,
        form.wiFiPassword,
        form.wiFiStealthMode,
        form.wiFiEncryptionScheme,
        form.wiFiFixedIpEnabled,
        form.fixedIpAddress,
        form.fixedSubnetMask,
        form.fixedDefaultGateway,
        form.fixedDnsServer
      )
    case 'ethernet':
      return generateEtherIni(
        form.ethernetFixedIpEnabled,
        form.fixedIpAddress,
        form.fixedSubnetMask,
        form.fixedDefaultGateway,
        form.fixedDnsServer
      )
    case 'mobile':
      return generateMobileIni(
        form.mobileApn,
        form.mobileUserName,
        form.mobilePassword
      )
  }
}

export const useLocalModeConfig = (
  initialForm: LocalModeConfigForm | null
): [LocalModeConfig | null, (_: LocalModeConfigForm) => void] => {
  const formToConfig = (
    form: LocalModeConfigForm | null
  ): LocalModeConfig | null => {
    if (form === null) return null

    const ini = networkFormToIni(form)

    return { device: 'LiveShellX', configString: ini, form: form }
  }

  const [
    localModeConfig,
    setLocalModeConfig,
  ] = useState<LocalModeConfig | null>(formToConfig(initialForm))

  const setLocalModeForm = (form: LocalModeConfigForm) => {
    const newConfig = Object.assign({}, formToConfig(form))
    setLocalModeConfig(newConfig)
  }

  return [localModeConfig, setLocalModeForm]
}
