import React from 'react'
import { RadioButton } from '../../atoms/RadioButton'
import { useTranslation } from 'react-i18next'
import { useFormContext } from 'react-hook-form'
import { WiFiConfig, WiFiConfigure } from '../../molecules/WiFiConfigure'
import {
  FixedIpConfig,
  FixedIpConfigure,
} from '../../molecules/FixedIpConfigure'
import {
  MobileNetworkConfig,
  MobileNetworkConfigure,
} from '../../molecules/MobileNetworkConfigure'
import { CheckBox } from '../../atoms/CheckBox'

export interface NetworkConfig
  extends WiFiConfig,
    FixedIpConfig,
    MobileNetworkConfig {
  networkType: 'wiFi' | 'ethernet' | 'mobile'
  ethernetFixedIpEnabled: boolean
}

export const NetworkConfigure: React.FC = () => {
  const { t } = useTranslation()
  const { watch, register } = useFormContext<NetworkConfig>()
  const watchNetworkType = watch('networkType', 'wiFi')
  const watchFixedIpEnabled = watch('ethernetFixedIpEnabled', false)
  return (
    <div className="flex flex-col border-2">
      <div className="border-b-2">
        <div className="p-4">
          <RadioButton
            name={'networkType'}
            title={t('Wi-Fi (Wireless LAN)')}
            value={'wiFi'}
            initialChecked={true}
            ref={register}
          />
        </div>
        {watchNetworkType == 'wiFi' && (
          <div className="p-4 pl-8 border-t bg-gray-50">
            <WiFiConfigure />
          </div>
        )}
      </div>
      <div className="p-4">
        <RadioButton
          name={'networkType'}
          title={t('Ethernet')}
          value={'ethernet'}
          ref={register}
        />
      </div>
      {watchNetworkType == 'ethernet' && (
        <div className="p-4 pl-8 border-t bg-gray-50">
          <CheckBox
            name={'ethernetFixedIpEnabled'}
            title={t('Use fixed IP address')}
            ref={register}
          />
          {watchFixedIpEnabled && <FixedIpConfigure />}
        </div>
      )}
      <div className="border-t-2">
        <div className="p-4">
          <RadioButton
            name={'networkType'}
            title={t('3G/4G Network')}
            value={'mobile'}
            ref={register}
          />
        </div>
        {watchNetworkType == 'mobile' && (
          <div className="p-4 pl-8 border-t bg-gray-50">
            <MobileNetworkConfigure />
          </div>
        )}
      </div>
    </div>
  )
}
