import React from 'react'
import { PageTemplate } from '../../templates/PageTemplate'
import {
  LocalModeConfigForm,
  LocalModeConfigure,
} from '../../templates/LocalModeConfigure'
import { withRouter, RouteComponentProps } from 'react-router-dom'
import { LocalModeConfig } from '../../../useLocalModeConfig'

export type IndexProps = {
  localModeConfig?: LocalModeConfig | null
  setLocalModeConfigForm?: (_: LocalModeConfigForm) => void
}

export const Index = withRouter((props: RouteComponentProps & IndexProps) => {
  return (
    <PageTemplate>
      <LocalModeConfigure
        history={props.history}
        localModeConfig={props.localModeConfig}
        setLocalModeConfigForm={props.setLocalModeConfigForm}
      />
    </PageTemplate>
  )
})
