import React from 'react'
import { MdCheck } from 'react-icons/all'
import styles from './index.module.css'

export type CheckBoxProps = {
  name: string
  title: string
  initialChecked?: boolean
}

export const CheckBox = React.forwardRef<HTMLInputElement, CheckBoxProps>(
  function CheckBoxContent({ name, title, initialChecked = false }, ref) {
    return (
      <div>
        <label className="flex space-x-3">
          <input
            type="checkbox"
            name={name}
            className="hidden"
            defaultChecked={initialChecked}
            ref={ref}
          />
          <MdCheck
            className={
              'w-6 h-6 p-0.5 border-2 rounded-md bg-white fill-current text-transparent ' +
              styles.check
            }
          />
          <span className="font-medium">{title}</span>
        </label>
      </div>
    )
  }
)
