import React, { useEffect, useMemo, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { Link } from 'react-router-dom'
import SetupCableLS2 from '../../../assets/connection/setup_cable_ls2.png'
import SetupCableLSPro from '../../../assets/connection/setup_cable_lspro.png'
import SetupCableLSX from '../../../assets/connection/setup_cable_lsx.png'
import { Button } from '../../atoms/Button'
import { MdFileDownload, MdPlayArrow } from 'react-icons/all'
import { LocalModeConfig } from '../../../useLocalModeConfig'
import * as H from 'history'
import { AFSKModulate } from '../../../afskModem'

export type SoundGeneratedProps = {
  history?: H.History<H.LocationState>
  localModeConfig: LocalModeConfig | null
}

const samplingRate = {
  LiveShell2: 16000,
  LiveShellPro: 44100,
  LiveShellX: 48000,
}

export const SoundGenerated: React.FC<SoundGeneratedProps> = ({
  history,
  localModeConfig,
}) => {
  window.AudioContext =
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    window.AudioContext || (window as any).webkitAudioContext
  const audioContext = useMemo(() => new AudioContext(), [])
  const { t } = useTranslation()
  const [audioBlob, setAudioBlob] = useState<Uint8Array | null>(null)
  useEffect(() => {
    if (localModeConfig === null) {
      history?.push('/')
      setAudioBlob(null)
    } else {
      // console.log(localModeConfig)
      const audio = AFSKModulate(
        localModeConfig.configString,
        samplingRate[localModeConfig.device]
      )
      setAudioBlob(audio)
    }
  }, [audioContext, history, localModeConfig])
  const setupImg = {
    LiveShell2: SetupCableLS2,
    LiveShellPro: SetupCableLSPro,
    LiveShellX: SetupCableLSX,
  }
  const play = () => {
    if (audioBlob === null) return
    const source = audioContext.createBufferSource()
    const copied = new ArrayBuffer(audioBlob.byteLength)
    new Uint8Array(copied).set(audioBlob)
    audioContext.decodeAudioData(
      copied,
      (decodedData) => {
        source.buffer = decodedData
        source.connect(audioContext.destination)
        source.start()
      },
      null
    )
  }
  const download = () => {
    if (audioBlob === null) return
    const blob = new Blob([audioBlob], { type: 'audio/wav' })
    const url = URL.createObjectURL(blob)
    const link = document.createElement('a')
    document.body.appendChild(link)
    link.href = url
    link.setAttribute('download', 'liveshell_config.wav')
    link.click()
    link.remove()
    setTimeout(() => {
      URL.revokeObjectURL(url)
    }, 1e4)
  }
  if (localModeConfig === null) {
    return <div />
  }
  return (
    <div className="space-y-6">
      <h3 className="text-3xl md:text-4xl">
        {t('Send the settings to your LiveShell')}
      </h3>
      <hr />
      <div>
        <Link to="/">
          <p className="underline text-blue-900">
            {t('<< Back to input page')}
          </p>
        </Link>
      </div>
      <div className="flex flex-col items-center space-y-2">
        <img src={setupImg[localModeConfig.device]} alt="" />
        <p>{t('connection guide message')}</p>
      </div>
      {audioBlob ? (
        <div className="flex flex-col md:flex-row justify-center items-center space-y-4 space-x-6">
          <Button
            name={'play'}
            label={t('Play')}
            icon={MdPlayArrow}
            size="x-large"
            style="primary"
            onClick={play}
          />
          <span>{t('or')}</span>
          <Button
            name={'download'}
            label={t('Download')}
            icon={MdFileDownload}
            size="medium"
            style="secondary"
            onClick={download}
          />
        </div>
      ) : (
        <div>
          <p>{t('Generating...')}</p>
        </div>
      )}
    </div>
  )
}
