import React from 'react'

export type SelectorProps = {
  name: string
  title: string
  options: { value: string; title: string }[]
  initialSelected?: string
}

export const Selector = React.forwardRef<HTMLSelectElement, SelectorProps>(
  function SelectorContent({ name, title, options, initialSelected }, ref) {
    return (
      <div>
        <p className="text-base font-medium m-1">{title}</p>
        <select
          name={name}
          className="border-2 p-2 rounded-lg"
          defaultValue={initialSelected}
          ref={ref}
        >
          {options.map((value) => {
            return (
              <option key={value.value} value={value.value}>
                {value.title}
              </option>
            )
          })}
        </select>
      </div>
    )
  }
)
