import React from 'react'

export type TextFieldProps = {
  name: string
  title: string
  placeholder: string
  initialText?: string
  enabled?: boolean
  error?: string
  message?: string
  autoComplete?: string
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void
}

export const TextField = React.forwardRef<HTMLInputElement, TextFieldProps>(
  function TextFieldContent(
    {
      name,
      title,
      placeholder,
      initialText,
      enabled = true,
      error,
      message,
      autoComplete = 'default',
      onChange,
    },
    ref
  ) {
    const defaultStyle = 'w-full border-2 p-2 rounded-lg'
    const errorStyle = ' border-red-500'
    const disableStyle = ' bg-gray-300'
    return (
      <div>
        <p className="text-base font-medium m-1">{title}</p>
        <input
          type="text"
          name={name}
          placeholder={placeholder}
          defaultValue={initialText}
          readOnly={!enabled}
          autoComplete={autoComplete}
          onChange={onChange}
          className={
            defaultStyle +
            (error == undefined ? '' : errorStyle) +
            (enabled ? '' : disableStyle)
          }
          ref={ref}
        />
        <p className="text-sm font-bold text-red-500 m-1">{error}</p>
        <p className={'text-sm m-1' + (message == undefined ? ' hidden' : '')}>
          {message}
        </p>
      </div>
    )
  }
)
