import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import {
  NetworkConfig,
  NetworkConfigure,
} from '../../organisms/NetworkConfigure'
import { FormProvider, useForm } from 'react-hook-form'
import { Button } from '../../atoms/Button'
import * as H from 'history'
import { LocalModeConfig } from '../../../useLocalModeConfig'

export type LocalModeConfigForm = NetworkConfig

export type LocalModeConfigureProps = {
  history?: H.History<H.LocationState>
  localModeConfig?: LocalModeConfig | null
  setLocalModeConfigForm?: (_: LocalModeConfigForm) => void
}

export const LocalModeConfigure: React.FC<LocalModeConfigureProps> = ({
  history,
  localModeConfig,
  setLocalModeConfigForm,
}) => {
  const { t } = useTranslation()
  const methods = useForm<LocalModeConfigForm>({
    mode: 'onBlur',
    defaultValues: localModeConfig?.form ?? undefined,
  })
  const [submit, setSubmit] = useState(false)
  const onSubmit = (data: LocalModeConfigForm) => {
    setLocalModeConfigForm?.(data)
    setSubmit(true)
  }
  useEffect(() => {
    if (submit) {
      history?.push('/sound')
      setSubmit(false)
    }
  }, [submit, history])
  return (
    <form onSubmit={methods.handleSubmit(onSubmit)}>
      <FormProvider {...methods}>
        <div className="space-y-6">
          <h3 className="text-4xl">{t('LiveShell X Network Setting Tool')}</h3>
          <hr />
          <div className="space-y-4">
            <h4 className="text-3xl">{t('Network Settings')}</h4>
            <NetworkConfigure />
          </div>
          <div className="flex justify-center">
            <Button name={'submit'} label={t('Next')} size="x-large" submit />
          </div>
        </div>
      </FormProvider>
    </form>
  )
}
