import React from 'react'
import { useTranslation } from 'react-i18next'
import { useFormContext } from 'react-hook-form'
import { TextField } from '../../atoms/TextField'

export interface MobileNetworkConfig {
  mobileUserName: string
  mobilePassword: string
  mobileApn: string
}

export const MobileNetworkConfigure: React.FC = () => {
  const { t } = useTranslation()
  const { register, errors } = useFormContext<MobileNetworkConfig>()
  return (
    <div className="flex flex-col space-y-4">
      <TextField
        name={'mobileUserName'}
        title={t('User name')}
        placeholder={''}
        ref={register({ required: true })}
        error={errors.mobileUserName && t('This field is required.')}
      />
      <TextField
        name={'mobilePassword'}
        title={t('Password')}
        placeholder={''}
        ref={register({ required: true })}
        error={errors.mobilePassword && t('This field is required.')}
      />
      <TextField
        name={'mobileApn'}
        title={t('APN')}
        placeholder={''}
        ref={register({ required: true })}
        error={errors.mobileApn && t('This field is required.')}
      />
    </div>
  )
}
