import React, { useEffect } from 'react'
import './App.css'
import i18n from 'i18next'
import { initReactI18next, useTranslation } from 'react-i18next'
import translationEn from './locales/en.json'
import translationJa from './locales/ja.json'
import { Index } from './components/pages/Index'
import { BrowserRouter, Route, Switch } from 'react-router-dom'
import { Sound } from './components/pages/Sound'
import { useLocalModeConfig } from './useLocalModeConfig'

i18n.use(initReactI18next).init({
  resources: {
    en: {
      translation: translationEn,
    },
    ja: {
      translation: translationJa,
    },
  },
  lng: 'ja',
  fallbackLng: 'en',
  keySeparator: false,
  nsSeparator: false,
  interpolation: {
    escapeValue: false,
  },
})

const App: React.FC = () => {
  const { t } = useTranslation()
  useEffect(() => {
    document.title = t('LiveShell Local Mode Settings')
  })
  const [localModeConfig, setLocalModeConfigForm] = useLocalModeConfig(null)
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path="/">
          <Index
            localModeConfig={localModeConfig}
            setLocalModeConfigForm={setLocalModeConfigForm}
          />
        </Route>
        <Route path="/sound">
          <Sound localModeConfig={localModeConfig} />
        </Route>
      </Switch>
    </BrowserRouter>
  )
}

export default App
