import React from 'react'
import { TextField } from '../../atoms/TextField'
import { useTranslation } from 'react-i18next'
import { useFormContext } from 'react-hook-form'
import { FixedIpConfig, FixedIpConfigure } from '../FixedIpConfigure'
import { CheckBox } from '../../atoms/CheckBox'
import { Selector } from '../../atoms/Selector'

export interface WiFiConfig extends FixedIpConfig {
  wiFiSsid: string
  wiFiPassword?: string
  wiFiStealthMode: boolean
  wiFiEncryptionScheme: 'WPA' | 'WEP_Open' | 'WEP_Shared' | 'None'
  wiFiFixedIpEnabled: boolean
}

export const WiFiConfigure: React.FC = () => {
  const { t } = useTranslation()
  const { watch, register, errors } = useFormContext<WiFiConfig>()
  const watchStealthMode = watch('wiFiStealthMode', false)
  const watchEncryptionScheme = watch('wiFiEncryptionScheme', 'WPA')
  const watchFixedIpEnabled = watch('wiFiFixedIpEnabled', false)
  return (
    <div className="flex flex-col space-y-4">
      <TextField
        name={'wiFiSsid'}
        title={t('SSID (Wireless network name)')}
        placeholder={''}
        ref={register({
          required: true,
        })}
        error={errors.wiFiSsid && t('This field is required.')}
      />
      <TextField
        name={'wiFiPassword'}
        title={t('Password (Wi-Fi passphrase)')}
        placeholder={''}
        ref={register({
          required: watchStealthMode && watchEncryptionScheme != 'None',
        })}
        enabled={!(watchStealthMode && watchEncryptionScheme == 'None')}
        error={errors.wiFiPassword && t('This field is required.')}
      />
      <CheckBox
        name={'wiFiStealthMode'}
        title={t('Use stealth-mode access point')}
        ref={register}
      />
      {watchStealthMode && (
        <div className="m-2 ml-12">
          <Selector
            name={'wiFiEncryptionScheme'}
            title={t('Wi-Fi encryption scheme')}
            options={[
              { value: 'WPA', title: t('WPA/WPA2') },
              { value: 'WEP_Open', title: t('WEP (Open)') },
              { value: 'WEP_Shared', title: t('WEP (Shared)') },
              { value: 'None', title: t('No password') },
            ]}
            ref={register}
          />
        </div>
      )}
      <div>
        <CheckBox
          name={'wiFiFixedIpEnabled'}
          title={t('Use fixed IP address')}
          ref={register}
        />
        {watchFixedIpEnabled && <FixedIpConfigure />}
      </div>
    </div>
  )
}
