import React from 'react'
import { PageTemplate } from '../../templates/PageTemplate'
import { RouteComponentProps, withRouter } from 'react-router-dom'
import { SoundGenerated } from '../../templates/SoundGenerated'
import { LocalModeConfig } from '../../../useLocalModeConfig'

export type SoundProps = {
  localModeConfig: LocalModeConfig | null
}

export const Sound = withRouter((props: RouteComponentProps & SoundProps) => {
  return (
    <PageTemplate>
      <SoundGenerated
        localModeConfig={props.localModeConfig}
        history={props.history}
      />
    </PageTemplate>
  )
})
